import ssPaymentPassword from '../../components/sanshui-payment-password'
export default {
	components: {
		ssPaymentPassword
	},
	data() {
		return {
			type: 0,
			phone: '',
			balance: 0,
			isSubmit: false, //是否提交中
			price: ''
		};
	},
	onShow() {
		this.$store.commit('judgeLogin'); //判断登录状态
	},
	onLoad(option) {
		this.type = option.type;
		this.getUserAccont();
	},
	methods: {
		getUserAccont() {
			//获取账户
			var _this = this;
			_this.$request.user
				.getUserData({})
				.then(data => {
					if (data.status == 1) {
						if (_this.type == 2) {
							_this.balance = data.data.gold_balance;
						} else {
							_this.balance = data.data.acer_truncatum;
						}
					} else {
						uni.showToast({
							icon: 'none',
							title: data.msg
						});
					}
				})
				.catch(err => {
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});

		},
		openPwd() {
			var _this = this;
			if (_this.phone == '') {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的转赠手机号'
				});
				return false;
			}
			if (_this.price == '' || parseFloat(_this.price) <= 0) {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的提现金额'
				});
				return false;
			}
			if (parseFloat(_this.price) > _this.balance) {
				uni.showToast({
					icon: 'none',
					title: '账户余额不足'
				});
				return false;
			}
			//验证密码
			this.$refs.paymentPassword.modalFun('show');
		},
		withdrawal(e) {
			//转赠
			var _this = this;
			if (_this.isSubmit) return false;
			if (_this.phone == '') {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的转赠手机号'
				});
				return false;
			}
			if (_this.price == '' || parseFloat(_this.price) <= 0) {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的转赠金额'
				});
				return false;
			}
			if (parseFloat(_this.price) > _this.balance) {
				uni.showToast({
					icon: 'none',
					title: '账户余额不足'
				});
				return false;
			}
			_this.isSubmit = true;
			uni.showLoading({
				title: '数据提交中'
			});
			_this.$request.account
				.tyransfer({
					type: _this.type,
					money: _this.price,
					phone: _this.phone,
					pwdPay: e.value
				})
				.then(data => {
					uni.hideLoading();
					_this.isSubmit = false;
					if (data.status == 1) {
						uni.showToast({
							icon: 'none',
							title: '申请成功'
						});
						setTimeout(() => {
							uni.navigateBack();
						}, 1000)
					} else {
						_this.$refs.paymentPassword.modalFun('hide');
						uni.showToast({
							icon: 'none',
							title: data.msg
						});
					}
				})
				.catch(err => {
					_this.$refs.paymentPassword.modalFun('hide');
					_this.isSubmit = false;
					uni.hideLoading();
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});
		}
	}
};
